@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500;600;700&family=Limelight&family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Source+Serif+4:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@layer base {
    * {
        @apply font-body text-light select-none;
    }
    h1 {
        @apply font-title text-4xl py-4;
    }
    h2 {
        @apply font-subtitle italic font-light text-2xl py-4;
    }
    .button{
        @apply flex justify-center items-center py-2 px-8 m-2 w-full font-bold rounded-full transform transition-all filter hover:-translate-y-2;
    }
}